.game-board-wrapper-outer {
    width: 100%;
    padding-top: 50%;
    position: relative;

    .game-board-wrapper-inner {
        display: flex;
        flex-wrap: wrap;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: salmon;
        
        .board-square {
            width: calc(100% / 16);
            height: calc(100% / 8);
            background-color: red;
            border: 1px solid black;
            box-sizing: border-box;

            &.img {
                background-image: url(../../../assets/svg/covid-straight-temp.svg);
                background-size: cover;
            }
            
            &.img2 {
                background-image: url(../../../assets/svg/covid-turn-temp.svg);
                background-size: cover;
            }
        }

        .enemy {
            position: absolute;
            right: 0;
            top: calc(100% / 16 * 8);
            width: 20px;
            height: 20px;
            background-color: pink;
            transform: translate(-50%, -50%);
            
            animation: move 6s linear forwards;
        }
    }
}

@keyframes move {
    100% {
        right: 90%;
    }
}