.turret {
    position: absolute;
    left: 50%;
    top: 70%;
    width: 30px;
    height: 30px;
    background-color: blue;

    .point {
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 10px;
        height: 500px;
        background-color: blue;
    }

    .radius-circle {
        width: 300px;
        height: 300px;
        border-radius: 50%;
        transform: translate(-50%, -50%);
        position: absolute;
        top: 50%;
        left: 50%;
        background-color: black;
        opacity: .2;
    }
}